
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;

}



ul {
  list-style: none
}

li::before {
  content: "\2022";
  color: #FA4A0C;
  display: inline-block;
  width: 1em;
  margin-left: -0.5em;
}


@font-face {
  font-family: customFont;
  src: url(IBMPlexSans-Light.ttf)
}


@font-face {
  font-family: customFont2;
  src: url(IBMPlexSans-Light.ttf)
}


body {
  background-color: #EDEDED;;
  background-color: white;
  font-family: customFont;
}

#projectGallContainer::-webkit-scrollbar { 
  display: none;  /* Safari and Chrome */
}

#project-grid-container {
  display: grid;
  grid-template-columns: 16rem 8rem 20rem;
  grid-template-rows: auto auto auto;
  column-gap: 10px;
  row-gap: 10px;
}
.project-grid-item {
  grid-column: 1/span 2;
  grid-row: 1/span 1; 
}

.project-grid-item:nth-child(2) {
  grid-column: 3/span 1;
  grid-row: 1/span 1; 
}

.project-grid-item:nth-child(3) {
  grid-column: auto/span 3;
  grid-row: auto/span 1; 
  
}

.project-grid-item:nth-child(4) {
  grid-column: 2/span 2;
  grid-row: 2/span 1; 
}
.project-grid-item:nth-child(5) {
  grid-column: 1/span 1;
  grid-row: 2/span 1; 
}

@media only screen and (max-width: 734px) {
  #project-grid-container  {
    grid-template-columns: auto;
    grid-auto-rows: auto;
    max-width: 25em;
    width: 100%;
}
#project-grid-container  * {
  grid-column: auto;
  grid-row: auto;
}


}


#contactview-container, #contact-card {
  backdrop-filter: blur(3px);
-webkit-backdrop-filter: blur(3px);
z-index: 1;
}

#contact-card  {
  backdrop-filter: blur(4px);
  -webkit-backdrop-filter: blur(4px);
}

